import * as React from "react";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import { io } from "socket.io-client";
const socket = io("http://168.138.70.111:1337", {});

const Alert = MuiAlert;
import { styled } from "@mui/material/styles";
import Paper from "@mui/material/Paper";
import { useLocalStorage } from '../hooks/useLocalStorage'
const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === "dark" ? "#1A2027" : "#fff",
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
}));
export const ProfilePage = () => {
  const [open, setOpen] = React.useState(false);
  const [message, setMessage] = React.useState("");
  const [kichHoatTaiXiuThuong, setKichHoatTaiXiuThuong] = React.useState(false);
  const [userData, setUser] = useLocalStorage("user", {});

  const handleClick = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  React.useEffect(() => {
    if (kichHoatTaiXiuThuong) {
      socket.on("message:create", async (data) => {
        console.log("create", data);
        const message = `|# ${data.Phien}|${data.KetQua}|Tỷ lệ ${data.TyLe} %`;
        await setMessage(message);
        await setOpen(true);
      });
      socket.on("message:update", async (data) => {
        console.log("update", data);
        const message = `|# ${data.Phien}|${data.KetQua}|Tỷ lệ ${data.TyLe} %`;
        await setMessage(message);
        await setOpen(true);
      });
    }

    return () => {
      socket.off();
    };
  }, [kichHoatTaiXiuThuong]);

  return (
    <>
      <Stack spacing={2} sx={{ width: "100%" }}>
        <Stack spacing={2}>
          <Button
            onClick={() => {
              if (!userData.user.kichHoat) {
                setMessage("Liên hệ admin để kích hoạt");
                setOpen(true);
                return
              }
              if (kichHoatTaiXiuThuong) {
                setMessage("Tắt tool  thành công");
                setOpen(true);
                setKichHoatTaiXiuThuong(false);
              } else {
                setMessage("Bật tool thành công");
                setOpen(true);
                setKichHoatTaiXiuThuong(true);
              }
            }}
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
          >
            TOOL TÀI XỈU THƯỜNG
          </Button>
          <Button
            onClick={() => {
              setMessage("Sáp ra mắt");
              setOpen(true);
            }}
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
          >
            TOOL TÀI XỈU VIP
          </Button>
          <Button
            onClick={() => {
              setMessage("Sáp ra mắt");
              setOpen(true);
            }}
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
          >
            TOOL HACK TIỀN
          </Button>
        </Stack>
        <Snackbar
          open={open}
          autoHideDuration={15000}
          onClose={handleClose}
          anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
          style={{ marginTop: 100 }}
        >
          <Alert
            onClose={handleClose}
            severity="success"
            sx={{ width: "100%" }}
          >
            {message}
          </Alert>
        </Snackbar>
      </Stack>
    </>
  );
};
